import React from 'react';
import Greeting from './Greeting/Greeting';

const GreetingContainer: React.FC = () => { // –¥–µ—Å—Ç—Ä—É–∫—Ç—É—Ä–∏–∑–∞—Ü–∏—è –ø—Ä–æ–ø—Å–æ–≤
    const [name, setName] = React.useState('');
    const [error, setError] = React.useState('');
    const [nameBox, setInNameBox] = React.useState<Array<string>>([])

    const setNameCallback = (name: string) => { 
        setName(name.trim()) 
    }

    const addUser = () => {
        if(name === '') {
            setError('–í–≤–≤–µ–¥–∏—Ç–µ, –∫–æ—Ä—Ä–µ–∫—Ç–Ω–æ–µ –∏–º—è');
        } else {
            alert(`Hello, ${name} !`);
            setInNameBox([name, ...nameBox]);
            setName('');
            setError('');
        }
    }

    const totalUsers = nameBox.length;

    return (
        <Greeting
            name={name}
            setNameCallback={setNameCallback}
            addUser={addUser}
            error={error}
            totalUsers={totalUsers}
        />
    )
}

export default GreetingContainer;