import React, {ButtonHTMLAttributes, DetailedHTMLProps} from 'react'

// —Ç–∏–ø –ø—Ä–æ–ø—Å–æ–≤ –æ–±—ã—á–Ω–æ–π –∫–Ω–æ–ø–∫–∏, children –≤ –∫–æ—Ç–æ—Ä–æ–º —Ö—Ä–∞–Ω–∏—Ç—å—Å—è –Ω–∞–∑–≤–∞–Ω–∏–µ –∫–Ω–æ–ø–∫–∏ —Ç–∞–º —É–∂–µ –æ–ø–∏—Å–∞–Ω
type DefaultButtonPropsType = DetailedHTMLProps<ButtonHTMLAttributes<HTMLButtonElement>, HTMLButtonElement>

type SuperButtonPropsType = DefaultButtonPropsType & {
    red?: boolean
}

const SuperButton: React.FC<SuperButtonPropsType> = ({red, className,...restProps}) => {    
    const finalClassName = `${red ? 'btn btn-danger' : 'btn btn-primary'} ${className}`

    return (
        <button
            className={'btn btn-primary ' + finalClassName}
            // –û—Ç–¥–∞—ë–º –∫–Ω–æ–ø–∫–µ –æ—Å—Ç–∞–ª—å–Ω—ã–µ –ø—Ä–æ–ø—Å—ã, –µ—Å–ª–∏ –æ–Ω–∏ –µ—Å—Ç—å (children –≤–Ω—É—Ç—Ä–∏).
            {...restProps} 
        />
    )
}

export default SuperButton
